import React, { useState } from "react";
import { Button, Form, Input, Row, Col } from 'antd';
import { RegisterFormItemLayout, RegisterTailFormItemLayout } from "../style";
import { handleSendCaptcha } from "../router";

export default function RegisterForm({ handleRegister }) {

    const [email, setEmail] = useState('');
    const [form] = Form.useForm();

    return (   
        <Form
            {...RegisterFormItemLayout}
            form={form}
            name="register"
            onFinish={handleRegister}
        >
            <Form.Item
                name="email"
                label=""
                rules={[
                    {
                        type: 'email',
                        message: '',
                    },
                    {
                        required: true,
                        message: '',
                    }
                ]}
                hasFeedback
            >
                <Input
                    value={email}
                    onChange={(e) => setEmail(e.target.value)} 
                />
            </Form.Item>

            <Form.Item
                name="pwd"
                label=""
                rules={[
                    {
                        required: true,
                        message: '',
                    }
                ]}
                hasFeedback
            >
                <Input.Password />
            </Form.Item>

            <Form.Item
                name="cpwd"
                label=""
                rules={[
                    {
                        required: true,
                        message: '',
                    },
                    ({ getFieldValue }) => ({
                        validator(_, value) {
                            if (!value || getFieldValue('pwd') === value) {
                                return Promise.resolve();
                            }
                            return Promise.reject(new Error(''));
                        },
                    }),
                ]}
            >
                <Input.Password />
            </Form.Item>
            
            <Form.Item
                label=""
            >
                <Row gutter={8}>
                    <Col span={12}>
                        <Form.Item
                            name="captcha"
                            rules={[
                                {
                                    required: true,
                                    message: '',
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Button
                            onClick={() => handleSendCaptcha(email)}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form.Item>

            
            <Form.Item {...RegisterTailFormItemLayout}>
                <Button
                    type="primary"
                    size="large"
                    htmlType="submit"
                    className="register-form-button"
                >
                    
                </Button>
            </Form.Item>
        </Form>
    );
}